import React from 'react';
import propTypes from 'prop-types';

import {Grid, Cell} from 'styled-css-grid';
import Zoom from 'react-medium-image-zoom';
import 'react-medium-image-zoom/dist/styles.css';

import useProject from '../../hooks/useProject';

import {Wrapper, GridWrapper, Img, Title, Text} from './styled';

const Project = props => {
    const {
        match: {
            params: {name: projectName},
        },
    } = props;

    const {project, getImage} = useProject(projectName);
    const {images, description, title} = project;

    return (
        <Wrapper>
            <GridWrapper>
                <Title>{title}</Title>
                <Text>{description}</Text>
                <Grid columns={10} gap="16px">
                    {images.map(({name, width}) => (
                        <Cell width={width}>
                            <Zoom>
                                <Img key={name} src={getImage(name)} />
                            </Zoom>
                        </Cell>
                    ))}
                </Grid>
            </GridWrapper>
        </Wrapper>
    );
};

export default Project;

Project.propTypes = {
    match: propTypes.object,
};
