export default function useProject(projectName) {
    const requestImageFile = require.context('../assets', true, /.jpg/);

    const getImage = name =>
        requestImageFile(`./${projectName}/${name}.jpg`).default;

    const project = config[projectName];
    return {project, getImage};
}
